import React from 'react'
import ServiBox from '../../components/ServiBox/ServiBox'
import './servicos.scss'
import Carousel from 'react-bootstrap/Carousel'

function Servicos() {
  return (
    <Carousel className="container-servicos" id='servicos'>
        <Carousel.Item>
            <ServiBox className='' 
            title2='sERVIOS Realizados'
            title='Identidade Visual'
            text='A identidade visual  a parte do site que mais importa para o seu site ou rede social.
             o que define sua personalidade e a imagem que voc quer passar para o pblico. Alm da construo
            de sites, tambm realizo a construo de uma identidade para sua pgina do Instagram, Facebook,
            YouTube entre outras redes sociais.'
            image={require('../../assets/id.jpg')}
            />
      </Carousel.Item>
      <Carousel.Item>
            <ServiBox className='' 
            title2='Servios Realizados'
            title='MODELO DE PORTIFLIO'
            text='O primeiro passo para desenvolver o seu portiflio Web  criar um modelo, para que seja possvel
            determnar o visual e as funcionalidades que voc quer expr para deixar seu site mais atrativo ao seu pblico.
            Realizo esse trabalho usando programas como Figma, Abode Photoshop e Adobe XD.'
            image={require('../../assets/proto.jpg')}
            />
      </Carousel.Item>
      <Carousel.Item>
            <ServiBox className='' 
            title2='Servios Realizados'
            title='PORTIFLIO WEB'
            text='Depois da prototipao  hora de construir o seu site e colocar no ar.
            Utilizando React JS ou apenas os recursos padres de Web (HTML, CSS, JS) construo o seu site. O deploy e o hosting fica
            a critrio do cliente, no estando inclusto no preo do servio.'
            image={require('../../assets/site.jpg')}
            />
      </Carousel.Item>
    </Carousel>
  )
}

export default Servicos